import { useState } from "react";
import {
  Box,
  Button,
  Flex,
  Stack,
  Heading,
  Text,
  Input,
  Link as ChakraLink,
  HStack,
  Checkbox,
  Field,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { FaGoogle, FaEye, FaEyeSlash } from "react-icons/fa";
import Link from "next/link";

const MotionBox = motion(Box);

export default function LoginPage() {
  const [showPassword, setShowPassword] = useState(false);

  const handleSignIn = (e: React.FormEvent) => {
    e.preventDefault();
    // TODO: Add real sign-in logic (NextAuth or API call)
    alert("Sign-in functionality not implemented yet.");
  };

  return (
    <Flex
      minH="100vh"
      align="center"
      justify="center"
      bg="gray.50"
      _dark={{ bg: "gray.900" }}
      p={4}
    >
      <MotionBox
        w="full"
        maxW="420px"
        initial={{ opacity: 0, y: 24 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.4, ease: "easeOut" }}
      >
        <Box
          bg="white"
          _dark={{ bg: "gray.800" }}
          p={8}
          borderRadius="2xl"
          boxShadow="2xl"
        >
          <Stack gap={6}>
            {/* Header */}
            <Stack textAlign="center">
              <Heading size="2xl" fontWeight="bold">
                Welcome Back
              </Heading>
              <Text color="gray.600" _dark={{ color: "gray.400" }}>
                Sign in to access your dashboard
              </Text>
            </Stack>

            {/* Form */}
            <Box as="form" onSubmit={handleSignIn}>
              <Stack gap={4}>
                {/* Email */}
                <Field.Root required>
                  <Field.Label>Email address</Field.Label>
                  <Input type="email" placeholder="name@company.com" />
                </Field.Root>

                {/* Password */}
                <Field.Root required>
                  <Field.Label>Password</Field.Label>
                  <Box position="relative">
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="••••••••"
                      pr="10"
                    />
                    <Button
                      type="button"
                      size="sm"
                      variant="ghost"
                      position="absolute"
                      right="1"
                      top="50%"
                      transform="translateY(-50%)"
                      onClick={() => setShowPassword((v) => !v)}
                    >
                      {showPassword ? <FaEyeSlash /> : <FaEye />}
                    </Button>
                  </Box>
                </Field.Root>

                {/* Remember / Forgot */}
                <HStack justify="space-between">
                  <Checkbox.Root>
                    <Checkbox.HiddenInput />
                    <Checkbox.Control />
                    <Checkbox.Label fontSize="sm">Remember me</Checkbox.Label>
                  </Checkbox.Root>

                  <ChakraLink fontSize="sm" color="teal.500" fontWeight="medium">
                    Forgot password?
                  </ChakraLink>
                </HStack>

                {/* Sign In */}
                <Button type="submit" size="lg" colorScheme="teal" w="full" mt={2}>
                  Sign in
                </Button>
              </Stack>
            </Box>

            {/* Divider */}
            <HStack>
              <Box flex="1" h="1px" bg="gray.200" />
              <Text fontSize="xs" color="gray.500" whiteSpace="nowrap">
                OR CONTINUE WITH
              </Text>
              <Box flex="1" h="1px" bg="gray.200" />
            </HStack>

            {/* Social */}
            <Button variant="outline" w="full">
              <FaGoogle style={{ marginRight: 8 }} />
              Continue with Google
            </Button>

            {/* Footer */}
            <Text textAlign="center" fontSize="sm">
              Don’t have an account?{" "}
              <ChakraLink asChild>
                <Link href="/signup" passHref>
                  <Text color="teal.500" fontWeight="bold" cursor="pointer">
                    Sign up
                  </Text>
                </Link>
              </ChakraLink>
            </Text>
          </Stack>
        </Box>
      </MotionBox>
    </Flex>
  );
}
